.gutter-padding {
  padding: $grid-gutter-width-base/2;
}

.gutter-padding-horizontal {
  padding-left: $grid-gutter-width-base/2;
  padding-right: $grid-gutter-width-base/2;
}

.gutter-padding-vertical {
  padding-top: $grid-gutter-width-base/2;
  padding-bottom: $grid-gutter-width-base/2;
}

.gutter-padding-top {
  padding-top: $grid-gutter-width-base/2;
}

.gutter-padding-bottom {
  padding-bottom: $grid-gutter-width-base/2;
}

.full-padding {
  padding: $grid-gutter-width-base;
}

.full-padding-horizontal {
  padding-left: $grid-gutter-width-base;
  padding-right: $grid-gutter-width-base;
}

.full-padding-vertical {
  padding-top: $grid-gutter-width-base;
  padding-bottom: $grid-gutter-width-base;
}

.padding-golden-ratio {
  padding: 50%/$golden-ratio 0;
}

.padding-none {
  padding: 0;
}
