.mdc-button {
  font-family: Roboto, sans-serif;
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased;
  font-size: 0.875rem;
  font-weight: 500;
  letter-spacing: 0.04em;
  line-height: 1.5rem;
  color: rgba(0, 0, 0, 0.87);
  color: var(--mdc-theme-text-primary-on-light, rgba(0, 0, 0, 0.87));
  display: inline-block;
  position: relative;
  min-width: 64px;
  height: 36px;
  padding: 0 16px;
  border: none;
  border-radius: 2px;
  outline: none;
  background: transparent;
  font-size: 14px;
  /* Override font to specifically be px as spec defined pt */
  font-weight: 500;
  line-height: 36px;
  /* Override line-height so text aligns centered */
  text-align: center;
  text-transform: uppercase;
  vertical-align: middle;
  box-sizing: border-box;
  -webkit-appearance: none;
  -webkit-tap-highlight-color: transparent;
  /* postcss-bem-linter: ignore */
  /* stylelint-disable selector-no-type */
}

.mdc-button--theme-dark,
.mdc-theme--dark .mdc-button {
  color: white;
  color: var(--mdc-theme-text-primary-on-dark, white);
}

.mdc-button::before {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  transition: opacity 120ms 0ms cubic-bezier(0.4, 0, 1, 1);
  border-radius: inherit;
  background: currentColor;
  content: "";
  opacity: 0;
}

.mdc-button:focus::before {
  transition: opacity 120ms 0ms cubic-bezier(0, 0, 0.2, 1);
  opacity: .12;
}

.mdc-button:active::before {
  transition: opacity 120ms 0ms cubic-bezier(0, 0, 0.2, 1);
  /*
      Slightly darker value for visual distinction.
      This allows a full base that has distinct modes.
      Progressive enhancement with ripples will provide complete button spec alignment.
    */
  opacity: .18;
}

.mdc-button:focus:active::before {
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
}

.mdc-button:active {
