.mdc-temporary-drawer__toolbar-spacer {
  display: flex;
  position: relative;
  flex-direction: row;
  flex-shrink: 0;
  align-items: flex-center;
  height: 56px;
  padding: 16px;
  border-bottom: 1px solid rgba(0, 0, 0, 0.12);
  box-sizing: border-box;
