.mdc-temporary-drawer__content {
  flex-grow: 1;
  margin: 0;
  overflow-x: hidden;
  overflow-y: auto;
  box-sizing: border-box;
  -webkit-overflow-scrolling: touch;
  touch-action: pan-y;
