.mdc-fab {
  display: inline-flex;
  position: relative;
  justify-content: center;
  width: 56px;
  height: 56px;
  padding: 0;
  transition: box-shadow 280ms cubic-bezier(0.4, 0, 0.2, 1);
  border: none;
  border-radius: 50%;
  cursor: pointer;
  user-select: none;
  box-sizing: border-box;
  fill: currentColor;
  -moz-appearance: none;
  -webkit-appearance: none;
  -webkit-tap-highlight-color: transparent;
  background-color: #FFA726;
  background-color: var(--mdc-theme-accent, #FFA726);
  color: rgba(0, 0, 0, 0.87);
  color: var(--mdc-theme-text-primary-on-accent, rgba(0, 0, 0, 0.87));
  box-shadow: 0px 3px 5px -1px rgba(0, 0, 0, 0.2), 0px 6px 10px 0px rgba(0, 0, 0, 0.14), 0px 1px 18px 0px rgba(0, 0, 0, 0.12);
  /*
    This allows for using SVGs within them to align properly in all browsers.
    Can remove once: https://bugzilla.mozilla.org/show_bug.cgi?id=1294515 is resolved.
  */
  /* stylelint-disable selector-no-type */
  /* postcss-bem-linter: ignore */
}

.mdc-fab--mini {
  width: 40px;
  height: 40px;
}

.mdc-fab--plain {
  background-color: white;
  color: rgba(0, 0, 0, 0.87);
  color: var(--mdc-theme-text-primary-on-light, rgba(0, 0, 0, 0.87));
}

.mdc-fab::before {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  transition: opacity 120ms 0ms cubic-bezier(0.4, 0, 1, 1);
  border-radius: inherit;
  background: black;
  content: "";
  opacity: 0;
}

.mdc-fab:focus::before {
  transition: opacity 120ms 0ms cubic-bezier(0, 0, 0.2, 1);
  opacity: .12;
}

.mdc-fab:active::before {
  transition: opacity 120ms 0ms cubic-bezier(0, 0, 0.2, 1);
  opacity: .18;
}

.mdc-fab:active:focus::before {
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
}

.mdc-fab:active, .mdc-fab:focus {
  outline: none;
}

.mdc-fab:active {
  box-shadow: 0px 7px 8px -4px rgba(0, 0, 0, 0.2), 0px 12px 17px 2px rgba(0, 0, 0, 0.14), 0px 5px 22px 4px rgba(0, 0, 0, 0.12);
}

.mdc-fab:hover {
  cursor: pointer;
}

.mdc-fab::-moz-focus-inner {
  padding: 0;
  border: 0;
}

.mdc-fab > svg {
  width: 100%;
}

fieldset:disabled .mdc-fab, .mdc-fab:disabled {
  background-color: rgba(0, 0, 0, 0.12);
  color: rgba(0, 0, 0, 0.26);
  cursor: default;
  pointer-events: none;
}

.mdc-fab__icon {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
}

